.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1200;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal_window {
  max-height: calc(100vh - 30px);
  max-width: calc(100vw - 60px);
}

.modal_window img {
  border-radius: 4px;
}
